import {Injectable} from '@angular/core';
import {Http, Response} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ImportPcdService {

  private BASE_URI = 'http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/imports';

  constructor(private http: Http) {}

  /**
   * this mothod for creating the new import
   * @param model 
   * @param file 
   */
  importSubmit(model, file: File) {
    const url_upload = this.BASE_URI + '?taskName='+ model.name + '&taskType=web';
    const formdata = new FormData();
    formdata.append('file', file);
    formdata.append('name', model.name);
    formdata.append('inputFileName', model.inputFileName);
    // alert(' Form Data : ' + formdata);
    return this.http.post(url_upload, formdata).map((response: Response) => <any>response.json());
  }
  
  /**
   * this mothod for updating the existing import
   * @param id 
   * @param model 
   * @param file 
   */
  reloadSubmit(id: any, file: File) {
    const formdata = new FormData();
    formdata.append('file', file);
    const url_reload = `${this.BASE_URI}/${id}`;
    // alert(' Form Data : ' + formdata);
    return this.http.put(url_reload, formdata).map((response: Response) => <any>response.json());
  }

}


