import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { InprogressImportsService } from './import-inprogress.service';
import { PaginationComponent } from '../../pagination/pagination.component';

@Component({
  selector: 'app-import-inprogress-list',
  template: require('./import-inprogress-list.component.html'),
  styles: [require('./import-inprogress-list.component.css')]
})
export class ImportInprogressListComponent implements OnInit {

  InprogressImports = [];
  errorMessage: 'Failed to get Data';
  public selectedImport: any = {};
  public uuid = "";
  public pageList = [];
  @ViewChild(PaginationComponent) public paginationComponent: any;

  constructor(private service: InprogressImportsService, private router: Router) {}
  ngOnInit(): any {
    this.service.getInprogressImports().subscribe(response => {
      this.InprogressImports = response,
      this.paginationComponent.exportTableList = this.InprogressImports;
      this.paginationComponent.initz();
    });
  }

  deleteInprogressImport(id: number) {

    if (confirm(' Are you sure, you want to Delete?')) {
      this.service.deleteInprogressImport(id).then(
        () => {
          this.service.getInprogressImports().subscribe(response => this.InprogressImports = response,
            error => this.errorMessage = <any>error);
        }
        
      );
    } else {
      this.router.navigate(['/scheduleImport']);
    }

  }

    onSelectionChange(item) {
        this.selectedImport = item;
        this.uuid = this.selectedImport.uuid;
        console.log('selected import' + this.selectedImport);
    }

    public getItems(){
      return this.paginationComponent.getItems();
    } 

}



