import { Http, Response } from '@angular/http';
import {Observable} from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

/**
 *  this class for service implementation of shedule import view/create
 *
 */
@Injectable()
export class CompletedImportsService {

  GET_COMPLETED_IMPORTS = 'http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/imports?status=complete';
  GET_DELETED_IMPORTS = 'http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/imports';
 
  constructor(private http: Http) {}

  getCompletedImports(): Observable<any> {
    return this.http.get(this.GET_COMPLETED_IMPORTS).map((response: Response) => <any>response.json()._embedded.importTaskBeanList);
  }

  /**
   * this method for delete existing SheduleImport
   * 
   * @param id
   */
  deleteCompletedImport(id) {
    const url = `${this.GET_DELETED_IMPORTS}/${id}`;
    return this.http.delete(url).toPromise();
  }

}



