import { ScheduledImportsService } from '../scheduled-imports/scheduled-imports.service';
import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import {  ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-schedule-import-view',
  template: require('./schedule-import-view.component.html'),
  styles: [require('./schedule-import-view.component.css')]
})
export class ScheduleImportViewComponent implements OnInit {
  public status: boolean;
  public data: any = {};
  public scheduleImports = [];
  public id: any;
  public freq = [];
  public day = [];
  /**
   * constructor
   * @param sheduleImportService
   * @param route 
   * @param http 
   */
  constructor(private service: ScheduledImportsService
    , private route: ActivatedRoute) { 
    this.freq = this.service.freq;
  }
  ngOnInit() {
    this.route.params.subscribe(params =>  {
     this.id = params['id'];
    });

   this.service.getScheduledImports().subscribe((res: any) =>  {
      this.scheduleImports = res;
     for ( const entry of this.scheduleImports) {
      if (entry.uuid === this.id) {
        let sts: string = entry.status; 
        if ( sts.toUpperCase() === 'ACTIVE') {
            this.status = true;
          } else {
            this.status = false;
          }
        this.data = entry;
        this.day = [this.data.freqPattern];
        break;
      }
  }

});
 }

}



