import {ScheduledImportsService} from './scheduled-imports.service';
import {Component, OnInit, ViewChild} from '@angular/core';
import {Router} from '@angular/router';
import { PaginationComponent } from '../../pagination/pagination.component';

@Component({
  selector: 'app-scheduled-imports',
  template: require('./scheduled-imports.component.html'),
  styles: [require('./scheduled-imports.component.css')]
})
export class ScheduledImportsComponent implements OnInit {
  scheduledImports = [];
  errorMessage: 'Failed to get Data';
  @ViewChild(PaginationComponent) public paginationComponent: any;
  constructor(private service: ScheduledImportsService, private router: Router) {}
  ngOnInit(): any {
    this.service.getScheduledImports().subscribe(response => {
      this.scheduledImports = response,
        this.paginationComponent.exportTableList = this.scheduledImports;
      this.paginationComponent.initz();
      error => this.errorMessage = <any>error
    });
  }

  /**
   * delete existing import task by id
   * @param id 
   */
  deleteScheduleImport(id: number) {

    if (confirm(' Are you sure, you want to Delete?')) {
      this.service.deleteSheduleImport(id).then(
        () => {
          this.service.getScheduledImports().subscribe(response => this.scheduledImports = response,
            error => this.errorMessage = <any>error);
        }
      );
    } else {
      this.router.navigate(['/scheduleImport']);
    }

  }
  
  public getItems() {
    return this.paginationComponent.getItems();
  }
}



