import { Http, Response } from '@angular/http';
import {Observable} from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

/**
 *  this class for service implementation of shedule import view/create
 *
 */
@Injectable()
export class InprogressImportsService {

  GET_INPROGRESS_IMPORTS = 'http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/imports?status=inprogress';
 
  constructor(private http: Http) {}

  getInprogressImports(): Observable<any> {
    return this.http.get(this.GET_INPROGRESS_IMPORTS).map((response: Response) => <any>response.json()._embedded.importTaskBeanList);
  }

  /**
   * this method for delete existing SheduleImport
   * 
   * @param id
   */
  deleteInprogressImport(id) {
    const url = `${'http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/imports'}/${id}`;
    return this.http.delete(url).toPromise();
  }

}



