import { Injectable } from '@angular/core';
import { Http, Response, ResponseContentType, Headers } from '@angular/http';
import { Observable } from 'rxjs';
import { Template } from './Template';
import { Company } from './Company';
import * as FileSaver from 'file-saver';

@Injectable()
export class ExportPcdService {

  baseURL = 'http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/';

  private POST_EXPORT_PCD = ' http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/exports';
  private GET_TEMPLATES = 'http://cscvappnor088.fsg.amer.csc.com:9000/v1/parameter/mass-maintenance/templates';
  constructor(private http: Http) { }

  getTemplates(): Observable<Template[]> {
    console.log('getTemplates() Service..!');
    return this.http.get(this.GET_TEMPLATES).map((response: Response) => <Template[]>response.json()._embedded.templateBeanList);
  }

  getParameterSet(id: any) {
    const url = `${this.GET_TEMPLATES}/${id}`;
    return this.http.get(url).map((response: Response) => response.json().psets);
  }

  exportData(paramSets) {
    const url = this.POST_EXPORT_PCD;
    const headers = new Headers({'Content-Type': 'application/json'});
    return this.http.post(url, paramSets, {headers: headers, responseType: ResponseContentType.Blob}).map(this.extractData).catch(this.handleError);
  }

  getcompanyDetailsByParameterId(id: number): Observable<any> {
     return this.http.get(this.baseURL + 'parameters/'+id+'/companies').map((response: Response) => <any>response.json());
  }
  
  private extractData(res: Response) {
    console.log(' response : ' + res);
    FileSaver.saveAs(new Blob([res.blob()],{ type: 'application/vnd.ms-excel'}), "download.xls");
}

  private handleError(error: any) {
    let errMsg = (error.message) ? error.message :
        error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg);
    return Observable.throw(errMsg);
}
    
}


